import React, { Component } from 'react'
import { Menu } from 'semantic-ui-react'

export default class MyMenu extends Component {
  handleItemClick = name => this.setState({ activeItem: name })

  render() {
    const { activeItem } = this.state || {}

    return (
      <Menu vertical>
        <Menu.Item>
          <Menu.Header>Konto</Menu.Header>

          <Menu.Menu>
            <Menu.Item
              name='Harmonogram'
              active={activeItem === 'Harmonogram'}
              onClick={this.handleItemClick}
            />
            <Menu.Item
              name='Znajomi'
              active={activeItem === 'Znajomi'}
              onClick={this.handleItemClick}
            />
            <Menu.Item
              name='Dane konta'
              active={activeItem === 'Dane konta'}
              onClick={this.handleItemClick}
            />
          </Menu.Menu>
        </Menu.Item>

        <Menu.Item>
          <Menu.Header>Wydarzenia</Menu.Header>

          <Menu.Menu>
            <Menu.Item
              name='Utwórz'
              active={activeItem === 'Utwórz'}
              onClick={this.handleItemClick}
            />
            <Menu.Item
              name='Znajdź'
              active={activeItem === 'Znajdź'}
              onClick={this.handleItemClick}
            />
            <Menu.Item 
              name='Bierzesz udział' 
              active={activeItem === 'Bierzesz udział'} 
              onClick={this.handleItemClick} />
          </Menu.Menu>
        </Menu.Item>

        <Menu.Item>
          <Menu.Header>Pomoc</Menu.Header>

          <Menu.Menu>
            <Menu.Item name='email' active={activeItem === 'email'} onClick={this.handleItemClick}>
              E-mail Support
            </Menu.Item>

            <Menu.Item name='faq' active={activeItem === 'faq'} onClick={this.handleItemClick}>
              FAQs
            </Menu.Item>
          </Menu.Menu>
        </Menu.Item>
      </Menu>
    )
  }
}
