import React from 'react';
import { Button, Form, Grid, Header, Message, Segment} from 'semantic-ui-react';
import { BrowserRouter as Route, Link } from "react-router-dom";

export default () => (
    <Grid centered columns={2}>
      <Grid.Column>
        <Header as="h2" textAlign="center">
          Logowanie do MeetMeAnyware
        </Header>
        <Segment>
          <Form size="large">
            <Form.Input
              fluid
              icon="user"
              iconPosition="left"
              placeholder="Login"
            />
            <Form.Input
              fluid
              icon="lock"
              iconPosition="left"
              placeholder="Hasło"
              type="password"
            />

            <Link to="/MeetMeAnyware/home">
              <Button onClick={() => {(alert('Zalogowano'))}} renderAs="button">
                  <span>Zaloguj</span>
              </Button>
            </Link>
          
          </Form>
        </Segment>
        {/* <Message>
          Nie masz jeszcze konta? <a href="/MeetMeAnyware/register">Zarejestruj się</a>
        </Message> */}
        <Message>
          Nie masz jeszcze konta?
          <Link to="/MeetMeAnyware/register"> Zarejestruj się</Link>
        </Message>
      </Grid.Column>
    </Grid>
);