import React from 'react';
import { Button, Form, Grid, Header, Segment} from 'semantic-ui-react';
import { BrowserRouter as Route, Link } from "react-router-dom";

export default () => (
  <Grid centered columns={2}>
    <Grid.Column>
      <Header as="h2" textAlign="center">
        Rejestracja do MeetMeAnyware
      </Header>
      <Segment>
        <Form size="large">
          <Form.Input
            fluid
            icon="user"
            iconPosition="left"
            placeholder="Login"
          />
          <Form.Input
            fluid
            icon="user"
            iconPosition="left"
            placeholder="Imie"
          />
          <Form.Input
            fluid
            icon="user"
            iconPosition="left"
            placeholder="Nazwisko"
          />
          <Form.Input
            fluid
            icon="mail"
            iconPosition="left"
            placeholder="e-mail"
          />
          <Form.Input
            fluid
            icon="lock"
            iconPosition="left"
            placeholder="Hasło"
            type="password"
          />
          <Form.Input
            fluid
            icon="lock"
            iconPosition="left"
            placeholder="Powtórz hasło"
            type="password"
          />

            <Link to="/MeetMeAnyware/home">
                <Button onClick={() => {(alert('Zarejestrowano'))}} renderAs="button">
                    <span>Zarejestruj</span>
                </Button>
            </Link>
        
        </Form>
      </Segment>
    </Grid.Column>
  </Grid>
);